.post-listing {
  padding: 0;
  margin: 0;
}

.post-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  padding-left: 1rem;

  &::before {
    content: "";
    position: absolute;
    left: 0.25em;
    width: 0.25em;
    height: 0.25em;
    background-color: var(--fgColor-accent);
    border-radius: 50%;
  }
}

.post-title {
  @extend %text-truncate;
  max-width: 40%;

  @media (max-width: 600px) {
    max-width: 60%;
  }
}

.post-separator {
  flex-grow: 1;
  border-bottom: 2px dotted var(--borderColor-muted);
  margin-inline: 0.5rem;
  height: 0.1rem;
  align-self: center;
}

.post-date {
  flex-shrink: 0;
  color: var(--fgColor-muted);
  font-size: 0.875rem;
}

.post-meta {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.5rem 1rem;
  font-size: clamp(0.9rem, 0.5vw + 0.6rem, 1rem);
  color: var(--fgColor-muted);
  margin-bottom: 1rem;

  > * {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
    line-height: 1.3;

    svg {
      width: 1.125em;
      height: 1.125em;
      flex-shrink: 0;
    }

    @include middle-dot-separator();
  }
}

.post__author {
  font-size: clamp(0.9rem, 0.5vw + 0.6rem, 1rem);
  color: var(--fgColor-muted);
  margin-top: 2rem;

  @media (max-width: 600px) {
    font-size: 0.8125rem;
  }

  em {
    font-weight: 500;
  }

  span[itemprop="name"] {
    color: var(--fgColor-default);
  }
}

.post-content {
  @extend %no-cursor;
}
